<template>
  <v-card v-if="jsonData" outlined class="pa-5">
    <strong>Rich message de tipo: {{getType(jsonData)}}</strong>
    <component :data="jsonData" :is="selectRichMessage(getType(jsonData))"></component>
  </v-card>
</template>

<script>
import richButton from "../richMessages/richButton";
import generic from "../richMessages/generic";
import { structProtoToJson } from "../../tools/structFunctions";
export default {
  props: ["data", "index"],
  data() {
    return {
      jsonData: null,
      components: [richButton, generic]
    };
  },
  mounted() {
    this.jsonData = this.data;
    this.jsonData = this.structToJson(this.jsonData);
  },
  methods: {
    getType(template) {
      return template.facebook.attachment.payload.template_type;
    },
    selectRichMessage(type) {
      switch (type) {
        case "button":
          return this.components[0];
          break;
        case "generic":
          return this.components[1];
          break;

        default:
          break;
      }
    },
    structToJson(struct) {
      return structProtoToJson(struct);
    }
  }
};
</script>

<style lang="scss" scoped>
</style>