<template>
  <v-container>
    <v-text-field label="Nombre del intent" v-model="intent.displayName"></v-text-field>
    <draggable handle=".handle" v-model="intent.messages" :animation="300">
      <transition-group>
        <div v-for="(message,i) in intent.messages" :key="i">
          <div v-if="message.hasOwnProperty('text')">
            <v-card class="mb-3">
              <v-toolbar class="handle" color="primary" dark>
                <v-toolbar-title>Mensaje multiple</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="deleteResponse(i)">Eliminar</v-btn>
              </v-toolbar>
              <v-container>
                <div v-for="(text,i) in message.text.text">
                  <v-layout row>
                    <v-text-field v-model="message.text.text[i]"></v-text-field>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </div>
          <div v-if="message.hasOwnProperty('payload')">
            <v-card class="mb-3">
              <v-toolbar class="handle" color="primary" dark>
                <v-toolbar-title>Mensaje en formato JSON</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn color="error" @click="deleteResponse(i)">Eliminar</v-btn>
              </v-toolbar>
              <rich-message ref="richMessageRef" :index="i" :data="message.payload"></rich-message>
            </v-card>
          </div>
        </div>
      </transition-group>
    </draggable>
    <v-btn color="error" text :to="{name:'intent'}">Atras</v-btn>
    <v-btn color="success" @click="updateIntent(intent)">Guardar</v-btn>
    <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <v-btn color="primary" v-on="on">Añadir respuesta</v-btn>
      </template>
      <v-list>
        <v-list-item v-for="(item, index) in options" :key="index" @click="createResponse(item.id)">
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-overlay :value="overlay">
      <v-progress-circular :size="70" :width="7" color="purple" indeterminate></v-progress-circular>
      <v-layout justify-center>
        <strong>Actualizando</strong>
      </v-layout>
    </v-overlay>
  </v-container>
</template>

<script>
import axios from "axios";
import draggable from "vuedraggable";
import { structProtoToJson, jsonToStructProto } from "../tools/structFunctions";
import {
  simpleMessage,
  buttonMessage,
  carruselButton
} from "../tools/richMessageTemplate";
export default {
  components: {
    draggable
  },
  data() {
    return {
      options: [
        { id: 1, title: "Mensaje simple" },
        { id: 2, title: "Boton" },
        { id: 3, title: "Menú carrusel" }
      ],
      intent: {},
      overlay: false
    };
  },
  mounted() {
    this.getInitialData();
  },
  methods: {
    getInitialData() {
      this.intent = this.$store.state.intents.find(element =>
        element.name.includes(this.$route.params.id)
      );
    },
    updateIntent(newIntent) {
      this.overlay = true;
      this.$refs.richMessageRef.forEach(richMessage => {
        let index = richMessage.index;
        let newRichMessage = richMessage.jsonData;
        newIntent.messages[index].payload = this.jsonToStruct(newRichMessage);
      });
      axios
        .put("/api/chatbot/agent/intents/update", { newIntent })
        .then(res => {
          if (res.data.ok) {
            console.log("intent actualizado con exito");
            this.overlay = false;

            console.log(res.data);
          } else {
            // error msg
            console.error(res.data);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    createResponse(id) {
      let jsonIntent = this.intent;
      switch (id) {
        case 1:
          console.log("para pushear: ", jsonIntent);
          jsonIntent.messages.push(simpleMessage());
          break;
        case 2:
          console.log("seleccionado: ", buttonMessage());
          jsonIntent.messages.push(buttonMessage());

          break;
        case 3:
          console.log("seleccionado: ", carruselButton());
          jsonIntent.messages.push(carruselButton());

          break;

        default:
          break;
      }
      this.intent = jsonIntent;
    },
    deleteResponse(id) {
      console.log("se eliminara: ", id);
      this.intent.messages.splice(id, 1);
      // console.log("Eliminando: ", id);
    },
    jsonToStruct(json) {
      return jsonToStructProto(json);
    },
    structToJson(struct) {
      return structProtoToJson(struct);
    }
  }
  // computed: {
  //   simpleText() {
  //     return this.intent.message.text.text
  //   }
  // },
};
</script>

<style lang="scss" scoped>
</style>